@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100%;

    &__header {
        width: 100%;
        text-align: center;
        background-color: blue;
    }
    
    &__contenido {
        width: 100%;
        height: 100%;
        background-color: green;
    }
}
